img{
    width: 100%;
    height:100%;
}
// doyoga-home
.banner{
    background:url("https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/yoga-week8/photo-1612157777902-5382bc6e864b.jpeg")
    center center no-repeat;
    background-size:cover ;
    height: 582px;
    .container{
        padding-top: 175px ;
        padding-bottom: 175px ;
    }
}
.fitness-pic{
    background:url("https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/yoga-week8/photo-1593810450967-f9c42742e326.jpeg")
    center center no-repeat;
    background-size:cover ;
    height: 420px;
}
.relax-pic{
    background:url("https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/yoga-week8/photo-1593810451137-5dc55105dace.jpeg")
    center center no-repeat;
    background-size:cover ;
    height: 420px;
}
.sculpture-pic{
    background:url("https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/yoga-week8/photo-1599901860904-17e6ed7083a0.jpeg")
    center center no-repeat;
    background-size:cover ;
    height: 420px;
}
.space-pic-yoga1{
    height:444px ;
    @media(max-width:1140px){
        height: 266px;
    }
}
.space-pic-yoga2{
    height: 207px;
    @media(max-width:1140px){
        height: 117px;
    }
}
.personal-pic{
    height: auto;
}
.course{
    img{
        @media(max-width:992px){
            height: 150px;
        }
        @media(max-width:576px){
            height: 165px;
        }
    }
    .courseSwiper {
        width: 100%;
        height: 100%;
        &-slide{
            text-align: center;
            width: 80%;
            height: 80%;
            img {
                display: block;
                max-width: 445px;
                max-height: 445px;
                object-fit: cover;
            }
        }
    }
}
.choose{
    img{
        height: 238px;
    @media(max-width:992px){
        height: 320px;
        }
    }
}

